        alt=\"\"\n                        />\n                        <img\n                            v-else-if=\"sendProfile.imageOne\"\n                            :src=\"\n                                '/assets/wedding_planner/uploads/' +\n                                sendProfile.imageOne\n                            \"\n                            alt=\"\"\n                        />\n                        <img\n                            v-else\n                            src=\"/assets/vendors/dashboard/ven-logo-upload.png\"\n                            alt=\"\"\n                        />\n                        <span>Add</span>\n                    </label>\n                    <label v-if=\"companyfileOpen\" for=\"company-logo\">\n                        <img\n                            v-if=\"companyPicPreview\"\n                            :src=\"companyPicPreview\"\n                            alt=\"\"\n                        />\n                        <img\n                            v-else-if=\"companyProfile.imageOne\"\n                            :src=\"\n                                '/assets/wedding_planner/uploads/' +\n                                companyProfile.imageOne\n                            \"\n                            alt=\"\"\n                        />\n                        <img\n                            v-else\n                            src=\"/assets/vendors/dashboard/ven-logo-upload.png\"\n                            alt=\"\"\n                        />\n                        <span>Add</span>\n                    </label>\n                </div>\n            </div>\n            <div class=\"row about\">\n                <div class=\"btn-container\">\n                    <button\n                        type=\"button\"\n                        class=\"tab-btn active\"\n                        data-id=\"vendor-details\"\n                        title=\"Personal Profile\"\n                        @click=\"\n                            toggleForm($event);\n                            companyfileOpen = false;\n                        \"\n                    ></button>\n                    <button\n                        type=\"button\"\n                        class=\"tab-btn\"\n                        data-id=\"company-details\"\n                        title=\"Company Profile\"\n                        @click=\"\n                            toggleForm($event);\n                            companyfileOpen = true;\n                        \"\n                    ></button>\n                    <!-- <button\n                        type=\"button\"\n                        class=\"tab-btn\"\n                        data-id=\"vendor-categories\"\n                        @click=\"toggleForm($event)\"\n                    ></button> -->\n                </div>\n                <div class=\"about-content\">\n                    <form\n                        @submit.prevent=\"\n                            toggleForm($event);\n                            submitProfile($event);\n                        \"\n                        class=\"content active\"\n                        id=\"vendor-details\"\n                        data-id=\"company-details\"\n                    >\n                        <input\n                            type=\"file\"\n                            id=\"vendor-logo\"\n                            name=\"vendor-logo\"\n                            required\n                            style=\"\n                                left: 42%;\n                                position: absolute;\n                                top: 13%;\n                                opacity: 0;\n                            \"\n                            @change=\"uploadImg($event, 'profile')\"\n                        />\n                        <h1>Personal details</h1>\n                        <div\n                            v-if=\"exist == 1\"\n                            style=\"display: flex; justify-content: flex-end\"\n                        >\n                            <button\n                                @click=\"editForm($event)\"\n                                style=\"\n                                    outline: none;\n                                    border: none;\n                                    background-color: #461952;\n                                    color: white;\n                                    padding: 4px 20px;\n                                    border-radius: 10px;\n                                    margin-bottom: 10px;\n                                    margin-right: 20px;\n                                \"\n                                type=\"button\"\n                            >\n                                Edit\n                            </button>\n                        </div>\n                        <div class=\"vendor-details-form-fields\">\n                            <label for=\"vendorFirstName\">\n                                First name\n                                <input\n                                    type=\"text\"\n                                    name=\"firstname\"\n                                    id=\"vendorFirstName\"\n                                    placeholder=\"Enter your first name\"\n                                    v-model=\"sendProfile.firstname\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.firstname\"\n                                    class=\"errMsg\"\n                                    >{{ errors.firstname[0] }}</span\n                                >\n                            </label>\n                            <label for=\"vendorLastName\">\n                                Last name\n                                <input\n                                    type=\"text\"\n                                    name=\"lastname\"\n                                    id=\"vendorLastName\"\n                                    placeholder=\"Enter your last name\"\n                                    v-model=\"sendProfile.lastname\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.lastname\"\n                                    class=\"errMsg\"\n                                    >{{ errors.lastname[0] }}</span\n                                >\n                            </label>\n                            <label for=\"vendorFatherName\">\n                                Father's name\n                                <input\n                                    type=\"text\"\n                                    name=\"fathername\"\n                                    id=\"vendorFatherName\"\n                                    placeholder=\"Enter your father's name\"\n                                    v-model=\"sendProfile.fathername\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.fathername\"\n                                    class=\"errMsg\"\n                                    >{{ errors.fathername[0] }}</span\n                                >\n                            </label>\n                            <label for=\"vendorPhone-1\">\n                                Phone Number-1\n                                <input\n                                    type=\"text\"\n                                    name=\"mobile\"\n                                    id=\"vendorPhone-1\"\n                                    placeholder=\"Enter your phone number\"\n                                    v-model=\"sendProfile.mobile\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.mobile\"\n                                    class=\"errMsg\"\n                                    >{{ errors.mobile[0] }}</span\n                                >\n                            </label>\n                            <label for=\"vendorPhone-2\">\n                                Phone Number-2\n                                <input\n                                    type=\"text\"\n                                    name=\"mobile1\"\n                                    id=\"vendorPhone-2\"\n                                    placeholder=\"Enter your phone number\"\n                                    v-model=\"sendProfile.mobile1\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.mobile1\"\n                                    class=\"errMsg\"\n                                    >{{ errors.mobile1[0] }}</span\n                                >\n                            </label>\n                            <label for=\"vendorLandline\">\n                                Landline Number\n                                <input\n                                    type=\"text\"\n                                    name=\";andline\"\n                                    id=\"vendorLandline\"\n                                    placeholder=\"Enter your landline number\"\n                                    v-model=\"sendProfile.landline\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.landline\"\n                                    class=\"errMsg\"\n                                    >{{ errors.landline[0] }}</span\n                                >\n                            </label>\n                            <label for=\"vendorEmailAddress\">\n                                Email Address\n                                <input\n                                    type=\"text\"\n                                    name=\"email\"\n                                    id=\"vendorEmailAddress\"\n                                    placeholder=\"Enter your email address\"\n                                    v-model=\"sendProfile.email\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.email\"\n                                    class=\"errMsg\"\n                                    >{{ errors.email[0] }}</span\n                                >\n                            </label>\n                            <label for=\"vendorWatsapp-1\">\n                                Watsapp Number-1\n                                <input\n                                    type=\"text\"\n                                    name=\"whatsapp\"\n                                    id=\"vendorWatsapp-1\"\n                                    placeholder=\"Enter your whatsapp number\"\n                                    v-model=\"sendProfile.whatsapp\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.whatsapp\"\n                                    class=\"errMsg\"\n                                    >{{ errors.whatsapp[0] }}</span\n                                >\n                            </label>\n                            <label for=\"vendorWatsapp-2\">\n                                Watsapp Number-2\n                                <input\n                                    type=\"text\"\n                                    name=\"whatsapp1\"\n                                    id=\"vendorWatsapp-2\"\n                                    placeholder=\"Enter your whatsapp number\"\n                                    v-model=\"sendProfile.whatsapp1\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.whatsapp1\"\n                                    class=\"errMsg\"\n                                    >{{ errors.whatsapp1[0] }}</span\n                                >\n                            </label>\n                        </div>\n                        <div class=\"vendor-address-form-fields\">\n                            <label for=\"vendorComplexName\">\n                                Complex Name\n                                <input\n                                    type=\"text\"\n                                    name=\"complex\"\n                                    id=\"vendorComplexName\"\n                                    placeholder=\"Enter your complex name\"\n                                    v-model=\"sendProfile.complex\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.complex\"\n                                    class=\"errMsg\"\n                                    >{{ errors.complex[0] }}</span\n                                >\n                            </label>\n                            <label for=\"vendorArea\">\n                                Area\n                                <input\n                                    type=\"text\"\n                                    name=\"area\"\n                                    id=\"vendorArea\"\n                                    placeholder=\"Enter your area name\"\n                                    v-model=\"sendProfile.area\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.area\"\n                                    class=\"errMsg\"\n                                    >{{ errors.area[0] }}</span\n                                >\n                            </label>\n                            <label for=\"vendorDistrict\">\n                                District\n                                <input\n                                    type=\"text\"\n                                    name=\"district\"\n                                    id=\"vendorDistrict\"\n                                    placeholder=\"Enter your district name\"\n                                    v-model=\"sendProfile.district\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.district\"\n                                    class=\"errMsg\"\n                                    >{{ errors.district[0] }}</span\n                                >\n                            </label>\n                            <label for=\"vendorZipCode\">\n                                Zip Code\n                                <input\n                                    type=\"text\"\n                                    name=\"zipcode\"\n                                    id=\"vendorZipCode\"\n                                    placeholder=\"Enter your zip code\"\n                                    v-model=\"sendProfile.zipcode\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.zipcode\"\n                                    class=\"errMsg\"\n                                    >{{ errors.zipcode[0] }}</span\n                                >\n                            </label>\n                            <label for=\"vendorState\">\n                                State\n                                <input\n                                    type=\"text\"\n                                    name=\"state\"\n                                    id=\"vendorState\"\n                                    placeholder=\"Enter your state name\"\n                                    v-model=\"sendProfile.state\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.state\"\n                                    class=\"errMsg\"\n                                    >{{ errors.state[0] }}</span\n                                >\n                            </label>\n                            <label for=\"vendorCountry\">\n                                Country\n                                <input\n                                    type=\"text\"\n                                    name=\"country\"\n                                    id=\"vendorCountry\"\n                                    placeholder=\"Enter your country name\"\n                                    v-model=\"sendProfile.country\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.country\"\n                                    class=\"errMsg\"\n                                    >{{ errors.country[0] }}</span\n                                >\n                            </label>\n                            <!-- <label for=\"vendorLocationLink\">\n                                <input\n                                    type=\"text\"\n                                    name=\"map\"\n                                    id=\"vendorLocationLink\"\n                                    placeholder=\"Add location link\"\n                                    v-model=\"sendProfile.map\"\n                                />\n                                <button id=\"location-link\">\n                                    <img\n                                        src=\"/assets/vendors/dashboard/location-icon.svg\"\n                                        alt=\"\"\n                                    />\n                                    Insert Link\n                                </button>\n                            </label> -->\n                        </div>\n                        <div class=\"ven-profile-form-submit\">\n                            <button\n                                v-if=\"exist != '1'\"\n                                id=\"pprofilebtn\"\n                                :disabled=\"pprofileDisabled\"\n                                type=\"submit\"\n                            >\n                                Submit\n                            </button>\n                            <button\n                                v-else\n                                style=\"display: none\"\n                                id=\"pprofilebtn\"\n                                :disabled=\"pprofileDisabled\"\n                                type=\"submit\"\n                            >\n                                Update\n                            </button>\n                            <button\n                                data-id=\"company-details\"\n                                @click=\"\n                                    toggleForm($event);\n                                    companyfileOpen = true;\n                                \"\n                                type=\"button\"\n                            >\n                                <img src=\"/assets/vendors/Frame.svg\" alt=\"\" />\n                            </button>\n                        </div>\n                    </form>\n                    <form\n                        @submit.prevent=\"submitCProfile($event)\"\n                        class=\"content\"\n                        id=\"company-details\"\n                    >\n                        <input\n                            v-show=\"companyfileOpen\"\n                            type=\"file\"\n                            id=\"company-logo\"\n                            name=\"company-logo\"\n                            required\n                            style=\"\n                                left: 42%;\n                                position: absolute;\n                                top: 13%;\n                                opacity: 0;\n                            \"\n                            @change=\"uploadImg($event, 'company')\"\n                        />\n                        <h1>Company details</h1>\n                        <div\n                            v-if=\"cexist == 1\"\n                            style=\"display: flex; justify-content: flex-end\"\n                        >\n                            <button\n                                @click=\"editForm($event)\"\n                                style=\"\n                                    outline: none;\n                                    border: none;\n                                    background-color: #461952;\n                                    color: white;\n                                    padding: 4px 20px;\n                                    border-radius: 10px;\n                                    margin-bottom: 10px;\n                                    margin-right: 20px;\n                                \"\n                                type=\"button\"\n                            >\n                                Edit\n                            </button>\n                        </div>\n                        <div class=\"vendor-details-form-fields\">\n                            <label for=\"companyname\">\n                                Company name\n                                <input\n                                    type=\"text\"\n                                    name=\"companyname\"\n                                    id=\"companyname\"\n                                    placeholder=\"Enter your company name\"\n                                    v-model=\"companyProfile.companyname\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.firstname\"\n                                    class=\"errMsg\"\n                                    >{{ errors.firstname[0] }}</span\n                                >\n                            </label>\n                            <label for=\"companyinfo\">\n                                Company Description\n                                <input\n                                    type=\"text\"\n                                    name=\"companyinfo\"\n                                    id=\"companyinfo\"\n                                    placeholder=\"Enter your company description\"\n                                    v-model=\"companyProfile.companyinfo\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.lastname\"\n                                    class=\"errMsg\"\n                                    >{{ errors.lastname[0] }}</span\n                                >\n                            </label>\n                            <label for=\"caddress\">\n                                Company Address\n                                <input\n                                    type=\"text\"\n                                    name=\"caddress\"\n                                    id=\"caddress\"\n                                    placeholder=\"Enter your company address\"\n                                    v-model=\"companyProfile.address\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.fathername\"\n                                    class=\"errMsg\"\n                                    >{{ errors.fathername[0] }}</span\n                                >\n                            </label>\n                            <label for=\"cmap\">\n                                Map\n                                <input\n                                    type=\"text\"\n                                    name=\"cmap\"\n                                    id=\"cmap\"\n                                    placeholder=\"Map\"\n                                    v-model=\"companyProfile.map\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.mobile\"\n                                    class=\"errMsg\"\n                                    >{{ errors.mobile[0] }}</span\n                                >\n                            </label>\n                            <label for=\"latitude\">\n                                Latitude\n                                <input\n                                    type=\"text\"\n                                    name=\"latitude\"\n                                    id=\"latitude\"\n                                    placeholder=\"Latitude\"\n                                    v-model=\"companyProfile.latitude\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.mobile1\"\n                                    class=\"errMsg\"\n                                    >{{ errors.mobile1[0] }}</span\n                                >\n                            </label>\n                            <label for=\"longitude\">\n                                Longitude\n                                <input\n                                    type=\"text\"\n                                    name=\"longitude\"\n                                    id=\"longitude\"\n                                    placeholder=\"Longtitude\"\n                                    v-model=\"companyProfile.longitude\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.landline\"\n                                    class=\"errMsg\"\n                                    >{{ errors.landline[0] }}</span\n                                >\n                            </label>\n                            <label for=\"establish\">\n                                Establish\n                                <input\n                                    type=\"text\"\n                                    name=\"establish\"\n                                    id=\"establish\"\n                                    placeholder=\"Company established\"\n                                    v-model=\"companyProfile.establish\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.email\"\n                                    class=\"errMsg\"\n                                    >{{ errors.email[0] }}</span\n                                >\n                            </label>\n                            <label for=\"gst\">\n                                GST Number\n                                <input\n                                    type=\"text\"\n                                    name=\"gst\"\n                                    id=\"gst\"\n                                    placeholder=\"GST\"\n                                    v-model=\"companyProfile.gst\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.whatsapp\"\n                                    class=\"errMsg\"\n                                    >{{ errors.whatsapp[0] }}</span\n                                >\n                            </label>\n                            <label for=\"phone\">\n                                Landline Number\n                                <input\n                                    type=\"text\"\n                                    name=\"whatsapp1\"\n                                    id=\"phone\"\n                                    placeholder=\"Landline Number\"\n                                    v-model=\"companyProfile.phone\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.whatsapp1\"\n                                    class=\"errMsg\"\n                                    >{{ errors.whatsapp1[0] }}</span\n                                >\n                            </label>\n                        </div>\n                        <div class=\"vendor-address-form-fields\">\n                            <!-- <label for=\"vendorCategories\">\n                                Vendor categories\n                                <select\n                                    name=\"vendorCategories\"\n                                    id=\"vendorCategories\"\n                                    v-model=\"catId\"\n                                    multiple\n                                    required\n                                >\n                                    <option hidden disabled selected>\n                                        Select vendorâ€™s category\n                                    </option>\n                                    <option\n                                        v-for=\"(\n                                            item, index\n                                        ) in defaultCategories\"\n                                        :key=\"index\"\n                                        :value=\"item.id\"\n                                    >\n                                        {{ item.name }}\n                                    </option>\n                                </select>\n                            </label> -->\n                            <label for=\"vendorCategories\">\n                                Location\n                                <select\n                                    name=\"vendorCategories\"\n                                    id=\"vendorCategories\"\n                                    v-model=\"companyProfile.locationmaster_id\"\n                                    required\n                                >\n                                    <option disabled selected>\n                                        Select Location\n                                    </option>\n                                    <option\n                                        v-for=\"(item, index) in locationMaster\"\n                                        :key=\"index\"\n                                        :value=\"item.id\"\n                                    >\n                                        {{ item.name }}\n                                    </option>\n                                </select>\n                            </label>\n                            <label for=\"mobile\">\n                                Primary Mobile\n                                <input\n                                    type=\"text\"\n                                    name=\"mobile\"\n                                    id=\"mobile\"\n                                    placeholder=\"Primary Mobile\"\n                                    v-model=\"companyProfile.mobile\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.complex\"\n                                    class=\"errMsg\"\n                                    >{{ errors.complex[0] }}</span\n                                >\n                            </label>\n                            <label for=\"mobile1\">\n                                Alternate Mobile\n                                <input\n                                    type=\"text\"\n                                    name=\"mobile1\"\n                                    id=\"mobile1\"\n                                    placeholder=\"Alternate Mobile \"\n                                    v-model=\"companyProfile.mobile1\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.area\"\n                                    class=\"errMsg\"\n                                    >{{ errors.area[0] }}</span\n                                >\n                            </label>\n                            <label for=\"whatsup\">\n                                Whatsapp\n                                <input\n                                    type=\"text\"\n                                    name=\"whatsup\"\n                                    id=\"whatsup\"\n                                    placeholder=\"Enter your Watsapp number\"\n                                    v-model=\"companyProfile.whatsup\"\n                                    required\n                                />\n                                <span\n                                    v-if=\"errors && errors.district\"\n                                    class=\"errMsg\"\n                                    >{{ errors.district[0] }}</span\n                                >\n                            </label>\n                        </div>\n                        <!-- <div class=\"vendor-preview-upload\">\n                            Add Company Pictures\n                            <label for=\"vendorImagesUpload\">\n                                <div\n                                    id=\"vendor-preview-upload-dropzone\"\n                                    class=\"dropzone\"\n                                    @dragover.prevent=\"drago($event)\"\n                                    @dragleave.prevent=\"dragl($event)\"\n                                    @drop.prevent=\"dragdrop($event)\"\n                                >\n                                    <span>\n                                        <img\n                                            src=\"/assets/vendors/dashboard/ven-gallery-upload.png\"\n                                            alt=\"\"\n                                        />\n                                    </span>\n                                    <span>Add photos/ videos</span>\n                                    <span>or drag and drop</span>\n                                    <span></span>\n                                </div>\n                                <div id=\"vendor-preview-upload-dragtext\"></div>\n                                <input\n                                    type=\"file\"\n                                    name=\"vendorImagesUpload\"\n                                    id=\"vendorImagesUpload\"\n                                    hidden\n                                    multiple\n                                    @change=\"count($event)\"\n                                />\n                            </label>\n                        </div> -->\n                        <div class=\"ven-profile-form-submit\">\n                            <button\n                                v-if=\"cexist != '1'\"\n                                :disabled=\"cprofileDisabled\"\n                                data-id=\"vendor-categories\"\n                                id=\"submitPprofile\"\n                            >\n                                Submit\n                            </button>\n                            <button\n                                v-else\n                                style=\"display: none\"\n                                id=\"submitPprofile\"\n                                :disabled=\"cprofileDisabled\"\n                                type=\"submit\"\n                            >\n                                Update\n                            </button>\n                            <!-- <button\n                                data-id=\"vendor-categories\"\n                                @click=\"toggleForm($event)\"\n                                type=\"button\"\n                            >\n                                <img src=\"/assets/vendors/Frame.svg\" alt=\"\" />\n                            </button> -->\n                        </div>\n                    </form>\n                    <!-- <div class=\"content\" id=\"vendor-categories\">\n                        <h1>Select Categories</h1>\n                        <div class=\"vendor-address-form-fields\">\n                            <label for=\"vendorCategories\">\n                                Add Cateogories\n                                <select\n                                    name=\"vendorCategories\"\n                                    id=\"vendorCategories\"\n                                    v-model=\"catId\"\n                                    multiple\n                                >\n                                    <option hidden disabled selected>\n                                        Select vendorâ€™s category\n                                    </option>\n                                    <option\n                                        v-for=\"(\n                                            item, index\n                                        ) in defaultCategories\"\n                                        :key=\"index\"\n                                        :value=\"item.id\"\n                                    >\n                                        {{ item.name }}\n                                    </option>\n                                </select>\n                            </label>\n                            <div>\n                                <span\n                                    v-for=\"(item, index) in catId\"\n                                    :key=\"index\"\n                                >\n                                    {{\n                                        defaultCategories.find(\n                                            (x) => x.id === item\n                                        ).name\n                                    }}\n                                </span>\n                            </div>\n                             <label for=\"vendorLocationLink\">\n                                <input\n                                    type=\"text\"\n                                    name=\"map\"\n                                    id=\"vendorLocationLink\"\n                                    placeholder=\"Add location link\"\n                                    v-model=\"sendProfile.map\"\n                                />\n                                <button id=\"location-link\">\n                                    <img\n                                        src=\"/assets/vendors/dashboard/location-icon.svg\"\n                                        alt=\"\"\n                                    />\n                                    Insert Link\n                                </button>\n                            </label> \n                        </div>\n                        <div id=\"ven-profile-form-submit\">\n                            <button\n                                data-id=\"vendor-categories\"\n                                @click=\"submitCProfile($event)\"\n                                type=\"button\"\n                            >\n                                Submit\n                            </button>\n                        </div>\n                    </div> -->\n                </div>\n            </div>\n        </div>\n        <flashMessage :message=\"message\"></flashMessage>\n\n        <!-- <Transition>\n            <div\n                v-if=\"message\"\n                style=\"\n                    background-color: #d7f3e3;\n                    border: 1px solid #c3ecd5;\n                    border-radius: 0.25rem;\n                    padding: 10px;\n                    padding-right: 2em;\n                    color: #227444;\n                    min-width: 150px;\n                    position: fixed;\n                    bottom: 30px;\n                    right: 30px;\n                    padding: 1em;\n                    z-index: 9999;\n                \"\n            >\n                {{ message }}\n                <span\n                    @click=\"message = null\"\n                    style=\"\n                        color: black;\n                        margin-right: -10px;\n                        padding: 10px;\n                        font-size: 20px;\n                        cursor: pointer;\n                    \"\n                    >Ã—</span\n                >\n            </div>\n        </Transition> -->\n    </div>\n</template>\n\n<script>\nimport flashMessage from \"../FlashMessage.vue\";\nexport default {\n    components: {\n        flashMessage,\n    },\n    props: [\"vendorId\", \"exists\", \"cexists\"],\n    data() {\n        return {\n            companyProfile: {\n                companyname: null,\n                companyinfo: null,\n                address: null,\n                map: null,\n                latitude: null,\n                longitude: null,\n                establish: null,\n                company_id: this.vendorId,\n                gst: null,\n                phone: null,\n                mobile: null,\n                mobile1: null,\n                whatsup: null,\n                locationmaster_id: null,\n            },\n            sendProfile: {\n                area: null,\n                complex: null,\n                country: null,\n                district: null,\n                email: null,\n                fathername: null,\n                firstname: null,\n                landline: null,\n                lastname: null,\n                mobile: null,\n                mobile1: null,\n                state: null,\n                company_id: this.vendorId,\n                whatsapp: null,\n                whatsapp1: null,\n                zipcode: null,\n            },\n            sendProfile_imageOne: null,\n            companyProfile_logo: null,\n            exist: this.exists,\n            cexist: this.cexists,\n            profileId: null,\n            companyId: null,\n            vendorProfile: {},\n            vendorDetails: null,\n            errors: null,\n            sendCategory: [],\n            catId: [],\n            errors: null,\n            defaultCategories: null,\n            locationMaster: null,\n            message: null,\n            cprofileDisabled: false,\n            pprofileDisabled: false,\n            pedit: false,\n            cedit: false,\n            companyfileOpen: false,\n            profilePicPreview: null,\n            companyPicPreview: null,\n            profileDataSubmitted: false,\n            companyDataSubmitted: false,\n        };\n    },\n    methods: {\n        uploadImg(e, type) {\n            if (type == \"profile\") {\n                this.sendProfile_imageOne = e.target.files[0];\n                this.profilePicPreview = URL.createObjectURL(\n                    this.sendProfile_imageOne\n                );\n                if (this.sendProfile.imageOne) {\n                    delete this.sendProfile.imageOne;\n                }\n            }\n\n            if (type == \"company\") {\n                this.companyProfile_logo = e.target.files[0];\n                this.companyPicPreview = URL.createObjectURL(\n                    this.companyProfile_logo\n                );\n                if (this.companyProfile.imageOne) {\n                    delete this.companyProfile.imageOne;\n                }\n            }\n        },\n        editForm(e) {\n            let id = e.currentTarget.parentElement.parentElement.id;\n            $(`#${id} .ven-profile-form-submit button[type=submit]`).css(\n                \"display\",\n                \"block\"\n            );\n            $(`#${id} input`).removeAttr(\"disabled\");\n            $(\"#vendor-logo\").removeAttr(\"required\");\n            $(\"#company-logo\").removeAttr(\"required\");\n            $(`#${id} input`).eq(1).focus();\n            if (id == \"vendor-details\") {\n                this.pedit = true;\n                this.pprofileDisabled = false;\n            }\n            if (id == \"company-details\") {\n                this.cedit = true;\n                this.cprofileDisabled = false;\n            }\n        },\n        toggleForm(e) {\n            const btns = document.querySelectorAll(\".tab-btn\");\n            const articles = document.querySelectorAll(\".content\");\n            btns.forEach(function (btn) {\n                btn.classList.remove(\"active\");\n            });\n            btns.forEach(function (btn) {\n                if (btn.dataset.id == e.currentTarget.dataset.id) {\n                    btn.classList.add(\"active\");\n                }\n            });\n            articles.forEach(function (article) {\n                article.classList.remove(\"active\");\n            });\n            document\n                .getElementById(e.currentTarget.dataset.id)\n                .classList.add(\"active\");\n        },\n        submitProfile(e) {\n            var _this = this;\n            this.companyfileOpen = true;\n            this.pprofileDisabled = true;\n            const config = {\n                headers: { \"content-type\": \"multipart/form-data\" },\n            };\n            let formData = new FormData();\n            let data = JSON.stringify(_this.sendProfile);\n            let img = this.sendProfile_imageOne;\n            formData.append(\"img\", img);\n            formData.append(\"data\", data);\n            if (this.pedit) {\n                formData.append(\"_method\", \"PATCH\");\n                formData.append(\"type\", \"personal\");\n                let updateId = this.sendProfile.id;\n                if (this.profileId != null) {\n                    updateId = parseInt(this.profileId);\n                }\n                axios\n                    .post(`/api/company/profile/${updateId}`, formData, config)\n                    .then(function (response) {\n                        _this.message = \"Profile updated\";\n                        _this.sendProfile_imageOne = null;\n                        _this.updateEdit();\n                        $(\n                            `#vendor-details .ven-profile-form-submit button[type=submit]`\n                        ).css(\"display\", \"none\");\n                        setTimeout(function () {\n                            _this.message = null;\n                        }, 3000);\n                    })\n                    .catch((error) => {\n                        _this.errors = error.response.data.errors;\n                        console.log(error.response.data.errors);\n                    });\n                return;\n            }\n            formData.append(\"type\", \"personal\");\n            axios\n                .post(`/api/company/profile`, formData, config)\n                .then(function (response) {\n                    console.log(response.data);\n                    _this.message = \"Profile Uploaded\";\n                    _this.sendProfile_imageOne = null;\n                    _this.exist = 1;\n                    _this.profileId = response.data;\n                    _this.updateEdit();\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                })\n                .catch((error) => {\n                    _this.errors = error.response.data.errors;\n                });\n        },\n        submitCProfile(e) {\n            var _this = this;\n            this.cprofileDisabled = true;\n            //this.companyProfile.company_id = _this.vendorId;\n            const config = {\n                headers: { \"content-type\": \"multipart/form-data\" },\n            };\n            let formData = new FormData();\n            let data = JSON.stringify(_this.companyProfile);\n            let img = this.companyProfile_logo;\n            formData.append(\"img\", img);\n            formData.append(\"data\", data);\n            console.log(data);\n            if (this.cedit) {\n                formData.append(\"_method\", \"PATCH\");\n                formData.append(\"type\", \"company\");\n                let updateId = this.companyProfile.id;\n                if (this.companyId != null) {\n                    updateId = this.profileId;\n                }\n                axios\n                    .post(`/api/company/profile/${updateId}`, formData, config)\n                    .then(function (response) {\n                        _this.message = \"Company Profile updated\";\n                        _this.cexist = 1;\n                        _this.updateEdit();\n                        $(\n                            `#company-details .ven-profile-form-submit button[type=submit]`\n                        ).css(\"display\", \"none\");\n                        _this.companyId = response.data;\n                        setTimeout(function () {\n                            _this.message = null;\n                            window.location.href = \"/company/welcome\";\n                        }, 3000);\n                    })\n                    .catch((error) => {\n                        _this.errors = error.response.data.errors;\n                        console.log(error.response.data.errors);\n                    });\n                return;\n            }\n            formData.append(\"type\", \"company\");\n\n            axios\n                .post(`/api/company/profile`, formData, config)\n                .then(function (response) {\n                    _this.message = \"Company Profile Uploaded\";\n                    // _this.cexist = 1;\n                    // _this.updateEdit();\n                    // _this.companyId = response.data;\n                    window.location.href = \"/company/welcome\";\n                })\n                .catch((error) => {\n                    //console.log(error);\n                    _this.errors = error.response.data.errors;\n                    //console.log(error.response.data.errors);\n                });\n        },\n        getvendorprofile() {\n            var _this = this;\n            var v = this.vendorId;\n            axios\n                .get(`/api/company/profile/${v}`)\n                .then(function (response) {\n                    _this.vendorDetails = response.data[0];\n                    _this.locationMaster = response.data[2];\n                    if (response.data[1]) {\n                        _this.sendProfile = response.data[1];\n                        delete _this.sendProfile.created_at;\n                        delete _this.sendProfile.deleted_at;\n                        delete _this.sendProfile.updated_at;\n                    }\n                    if (response.data[3]) {\n                        _this.companyProfile = response.data[3];\n                        delete _this.companyProfile.created_at;\n                        delete _this.companyProfile.deleted_at;\n                        delete _this.companyProfile.updated_at;\n                    }\n                })\n                .catch((errors) => {\n                    console.log(\"Get All: \" + errors);\n                });\n        },\n        // getvendorcategories() {\n        //     var _this = this;\n        //     axios\n        //         .get(`/wedding/vendorMasterList`)\n        //         .then(function (response) {\n        //             _this.defaultCategories = response.data;\n        //         })\n        //         .catch((errors) => {\n        //             console.log(\"Get All: \" + errors);\n        //         });\n        // },\n        drago(e) {\n            e.preventDefault();\n            let dragTest = document.querySelector(\n                \"#vendor-preview-upload-dragtext\"\n            );\n\n            if ((e.currentTarget.className = \"dropzone\")) {\n                e.currentTarget.style.opacity = \"0\";\n                dragTest.textContent = \"Drop File to Upload\";\n            }\n        },\n        dragl(e) {\n            if ((e.currentTarget.className = \"dropzone\")) {\n                let dragTest = document.querySelector(\n                    \"#vendor-preview-upload-dragtext\"\n                );\n                dragTest.textContent = \"\";\n                e.currentTarget.style.opacity = \"1\";\n            }\n        },\n        dragdrop(e) {\n            let dragTest = document.querySelector(\n                \"#vendor-preview-upload-dragtext\"\n            );\n            dragTest.textContent = \"\";\n            e.currentTarget.style.opacity = \"1\";\n            let file = e.dataTransfer.files;\n            let number = file.length;\n            e.currentTarget.lastE